<template>
    <div class="container">
        <div class="wrap main_login">
            <div id="mainTitle">
                <h1>{{ title }}</h1>
            </div>
            <h2 id="subTitle">친구들의 사진과 동영상을 보려면 가입하세요.</h2>
            <face-book></face-book>
            <div class="goTo_login_wrap">
                <div></div>
                <div>또는</div>
                <div></div>
            </div>
            <v-form ref="form" action="">
                <div>
                    <v-text-field
                        v-model="user.user_id"
                        label="휴대폰 번호 또는 이메일 주소"
                        aria-label="휴대폰 번호 또는 이메일 주소"
                        aria-required="true"
                        name="emailOrPhone"
                        solo
                        counter="30"
                        @input="spanChange"
                        class="text-field"
                        :rules="user_id_rule"
                        :disabled="state == 'ins' ? false : true"
                    ></v-text-field>
                </div>
                <div>
                    <v-text-field
                        v-model="user.user_name"
                        label="성명"
                        aria-label="성명"
                        aria-required="true"
                        autocapitalize="off"
                        counter="30"
                        solo
                        class="text-field"
                        :rules="user_name_rule"
                    />
                </div>
                <div>
                    <v-text-field
                        v-model="user.user_nickname"
                        label="사용자 이름"
                        aria-label="사용자 이름"
                        aria-required="true"
                        autocapitalize="off"
                        counter="15"
                        solo
                        class="text-field"
                        :rules="user_nickname_rule"
                    />
                </div>
                <div>
                    <v-text-field
                        v-model="user.user_password"
                        label="비밀번호"
                        aria-label="비밀번호"
                        aria-required="true"
                        autocapitalize="off"
                        counter="30"
                        solo
                        class="text-field"
                        :rules="user_password_rule"
                    />
                </div>
                <div>
                    저희 서비스를 이용하는 사람이 회원님의 연락처 정보를
                    Instagram에 업로드했을 수도 있습니다. 더 알아보기
                </div>
                <button>가입</button>
            </v-form>
        </div>
        <div class="wrap sub_login">
            계정이 있으신가요? <a href="">로그인</a>
        </div>
        앱을 다운로드하세요.
        <div class="download_wrap">
            <div class="store_wrap">
                <img :src="app_src" alt="" />
            </div>
            <div class="store_wrap">
                <img :src="google_src" alt="" />
            </div>
        </div>
    </div>
</template>


<script>
import FaceBook from "@/sns/FaceBook.vue";

export default {
    components: {
        FaceBook,
    },
    data() {
        return {
            title: this.$store.state.title,
            app_src: require("@/assets/app_store.png"),
            google_src: require("@/assets/google_play.png"),
            state: "ins",
            user: this.$store.state.user,
            user_id_rule: [
                (v) => !!v || "아이디는 필수 입력사항입니다.",
                (v) =>
                    !(v && v.length >= 30) ||
                    "아이디는 30자 이상 입력할 수 없습니다.",
                (v) => {
                    const replaceV = v.replace(/(\s*)/g, "");
                    const pattern =
                        /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/;
                    return (
                        pattern.test(replaceV) || "이메일 형식으로 입력해주세요"
                    );
                },
            ],
            user_name_rule: [
                (v) => !!v || "성명은 필수 입력사항입니다.",
                (v) =>
                    !(v && v.length >= 30) ||
                    "성명은 30자 이상 입력할 수 없습니다.",
                (v) =>
                    !/[~!@#$%^&*()_+|<>?:{}]/.test(v) ||
                    "성명에는 특수문자를 사용할 수 없습니다.",
            ],
            user_nickname_rule: [
                (v) => !!v || "사용자 이름은 필수 입력사항입니다.",
                (v) =>
                    !(v && v.length >= 30) ||
                    "사용자 이름은 30자 이상 입력할 수 없습니다.",
                (v) =>
                    !/[~!@#$%^&*()_+|<>?:{}]/.test(v) ||
                    "사용자 이름에는 특수문자를 사용할 수 없습니다.",
            ],
            user_pw_rule: [
                (v) =>
                    this.state === "ins"
                        ? !!v || "패스워드는 필수 입력사항입니다."
                        : true,
                (v) =>
                    !(v && v.length >= 30) ||
                    "패스워드는 30자 이상 입력할 수 없습니다.",
            ],
        };
    },
    methods: {
        signUp() {
            this.$axios
                .post("api/users/signUp", {
                    user: this.user,
                })
                .then((result) => {
                    if (result.data.success == true) {
                        alert(result.data.message);
                        this.$router.push("/login");
                    }
                    if (result.data.success == false) {
                        alert(result.data.message);
                    }
                })
                .catch((error) => {
                    alert(error);
                    console.log(error);
                });
        },
    },
};
</script>

<style lang="scss" scoped>
/* 메인 로그인 */
.container {
    display: flex;
    width: 100%;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: "consolas";

    .wrap {
        background: rgb(255, 255, 255);
        border: 1px solid rgba(219, 219, 219, 1);
        box-sizing: border-box;
        #mainTitle {
            height: 50px;
            margin-top: 50px;
            h1 {
                font-size: 40px;
            }
        }
        #subTitle {
            color: rgba(var(--f52, 142, 142, 142), 1);
            font-size: 17px;
            font-weight: 600;
            line-height: 20px;
            margin: 0 40px 10px;
            text-align: center;
        }
        .goTo_login_wrap {
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin: 0 auto;
            width: 270px;
            height: 40px;
            div:nth-of-type(1) {
                width: 30%;
                border: solid 1px rgba(var(--i1d, 38, 38, 38), 0.2);
            }
            div:nth-of-type(2) {
                color: rgba(38, 38, 38, 0.5);
                font-size: 15px;
                font-weight: 600;
            }
            div:nth-of-type(3) {
                width: 30%;
                border: solid 1px rgba(var(--i1d, 38, 38, 38), 0.2);
            }
        }
        form {
            height: 50%;
        }
    }
    .main_login {
        height: 720px;
        width: 350px;
        margin: 30px 0 10px 0;
    }
    /* 메인 로그인 종료 */
    .sub_login {
        height: 70px;
        width: 350px;
    }
}
.download_wrap {
    display: flex;
    flex-flow: row;
    justify-content: space-around;
    width: 350px;
    .store_wrap:nth-of-type(1) {
        img {
            width: 133px;
        }
    }
    .store_wrap:nth-of-type(2) {
        img {
            width: 140px;
        }
    }
}

form {
    div {
        color: rgba(142, 142, 142, 1);
        font-size: 12px;
        line-height: 16px;
        margin: 30px auto;
        text-align: center;
    }
    .text-field {
        box-sizing: border-box;
        background: rgba(250, 250, 250, 1);
        width: 266px;
        height: 56px;
        color: rgba(38, 38, 38, 1);
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            Helvetica, Arial, sans-serif;
    }
}
::v-deep .v-label.v-field-label.v-field-label--floating {
    font-size: 7px;
    color: rgba(142, 142, 142, 1);
}
::v-deep .v-label.v-field-label {
    font-size: 14px;
}
</style>